footer {
    background: var(--color-primary);
    padding: 3rem;
    text-align: center;
    font-size: 0.9rem;
    margin-top:7rem;
}
footer a{
    color:var(--color-bg);
}
.footer__logo{
    font-size: 3rem;
    font-weight: 700;
    margin-bottom:4rem;
    display: inline-block;
}
.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    display: flex;
    border-radius: 0.7rem;
    border: 1px solid transparent;
}
.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyrights {
    margin-bottom: 0;
    color: var(--color-bg);
    text-align: left;
    margin-left: 0;
}



/* Media queries (Medium devices) */
@media screen and (max-width: 1024px){
    
}
/* Media queries (Small devices) */
@media screen and (max-width: 600px){
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}
    